<style lang="scss">
.page-container {
  max-width: 100vw;
  height: 100vh;
}
.md-app {
  min-height: 100%;
  border: 1px solid rgba(#000, 0.12);
}
.router-link-active {
  border-left: solid 3px #e8716d;
}
// Demo purposes only
.md-drawer {
  position: fixed !important;
  width: 230px;
  max-width: calc(100vw - 125px);
}
.menu {
  margin-left: 15px;
}
.md-list.md-dense .md-list-item-content > .md-avatar {
  margin-top: 10px;
  margin-bottom: 5px;
  margin-left: -5px;
}
.md-drawer-active > .md-drawer {
  top: 0px;
}
.md-drawer {
  top: -65px;
}
/*
  RESET STYLES NEED NO SCOPED
*/
.md-app-content {
  padding: 0px;
}

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s cubic-bezier(0.5, 0.5, 0.5, 0.5);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.md-app-scroller {
  // background-color: #ebeced;
}
.md-app-content {
  margin: 0 auto;
  //max-width: 1140px;
}
</style>

<template>
  <div class="page-container">
    <md-app>
      <md-app-drawer :md-active.sync="menuVisible" md-fixed md-persistent="mini">
        <md-toolbar class="md-transparent" md-elevation="0">
          <span>{{ $t("intranet.nav_title") }}</span>

          <div class="md-toolbar-section-end">
            <md-button class="md-icon-button md-dense" @click="toggleMenu">
              <md-icon>keyboard_arrow_left</md-icon>
            </md-button>
          </div>
        </md-toolbar>

        <md-list class="md-dense">
          <li class="md-list-item">
            <md-button class="md-icon-button menu" @click="toggleMenu" v-if="!menuVisible">
              <md-icon>menu</md-icon>
            </md-button>
          </li>

          <md-divider/>

          <md-list-item>
            <md-avatar>
              <img :src="$store.state.user.profile.photo" title="profile photo">
            </md-avatar>
            <span class="md-list-item-text">{{this.$store.state.user.profile.username}}</span>
          </md-list-item>

          <md-list-item to="/intranet/dashboard">
            <md-icon title="dashboard">dashboard</md-icon>
            <span class="md-list-item-text">{{ $t("intranet.nav_list_dashboard") }}</span>
          </md-list-item>

          <md-list-item to="/intranet/exercise">
            <md-icon title="exercises">school</md-icon>
            <span class="md-list-item-text">{{ $t("intranet.nav_list_exercises") }}</span>
          </md-list-item>

          <md-list-item to="/intranet/challenges">
            <md-icon title="challenges">timeline</md-icon>
            <span class="md-list-item-text">{{ $t("intranet.nav_list_progress") }}</span>
          </md-list-item>

          <md-divider/>

          <md-list-item to="/intranet/settings">
            <md-icon title="settings">settings</md-icon>
            <span class="md-list-item-text">{{ $t("intranet.nav_list_settings") }}</span>
          </md-list-item>

          <md-list-item @click="logout">
            <md-icon title="logout">power_settings_new</md-icon>
            <span class="md-list-item-text">{{ $t("intranet.nav_list_logout") }}</span>
          </md-list-item>
        </md-list>
      </md-app-drawer>

      <md-app-content>
        <transition name="slide-fade">
          <router-view/>
        </transition>
      </md-app-content>
    </md-app>

  </div>
</template>

<script>
export default {
  name: "Intranet",
  data() {
    return {
      menuVisible: false,
      showSnackbar: true,
      position: "center",
      duration: 1000,
      isInfinity: true
    };
  },
  methods: {
    toggleMenu() {
      this.menuVisible = !this.menuVisible;
    },
    logout() {
      this.$store.dispatch("auth/AUTH_LOGOUT").then(() => {
        this.$router.push("/");
      });
    }
  }
};
</script>
